export interface Task {
  id: string;
  title: string;
  description?: string;
  category?: string;
  priority: 'high' | 'normal';
  reminderTime?: string; // HH:mm形式の時刻
  reminderDays?: {
    sunday: boolean;
    monday: boolean;
    tuesday: boolean;
    wednesday: boolean;
    thursday: boolean;
    friday: boolean;
    saturday: boolean;
  };
  workTime?: number; // 作業時間（分）
  completed: boolean;
  createdAt: Date;
  updatedAt: Date;
  isLoopReminder?: boolean; // ループリマインダーの設定を追加
}

export type TaskFormData = Omit<Task, 'id' | 'createdAt' | 'updatedAt'>;

export const defaultReminderDays = {
  sunday: false,
  monday: false,
  tuesday: false,
  wednesday: false,
  thursday: false,
  friday: false,
  saturday: false,
};