import { Variants } from 'framer-motion';

// 共通のトランジション設定
const commonTransition = {
  type: 'spring',
  stiffness: 500,
  damping: 30,
};

// リストアイテムのアニメーション
export const listItemVariants: Variants = {
  hidden: {
    opacity: 0,
    y: 20,
    scale: 0.8,
  },
  visible: {
    opacity: 1,
    y: 0,
    scale: 1,
    transition: {
      ...commonTransition,
      duration: 0.3,
    },
  },
  exit: {
    opacity: 0,
    scale: 0.8,
    transition: {
      ...commonTransition,
      duration: 0.2,
    },
  },
};

// フォームのアニメーション
export const formVariants: Variants = {
  hidden: {
    opacity: 0,
    y: -20,
  },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      ...commonTransition,
      duration: 0.3,
    },
  },
  exit: {
    opacity: 0,
    y: -20,
    transition: {
      ...commonTransition,
      duration: 0.2,
    },
  },
};

// フォームフィールドのアニメーション
export const formFieldVariants: Variants = {
  hidden: {
    opacity: 0,
    x: -20,
  },
  visible: {
    opacity: 1,
    x: 0,
    transition: {
      ...commonTransition,
      duration: 0.2,
    },
  },
};

// タイマーのアニメーション
export const timerVariants: Variants = {
  hidden: {
    opacity: 0,
    y: -20,
    scale: 0.95,
  },
  visible: {
    opacity: 1,
    y: 0,
    scale: 1,
    transition: {
      ...commonTransition,
      duration: 0.3,
    },
  },
  exit: {
    opacity: 0,
    y: -20,
    scale: 0.95,
    transition: {
      ...commonTransition,
      duration: 0.2,
    },
  },
};

// ボタンのホバーアニメーション
export const buttonHoverVariants: Variants = {
  initial: {
    scale: 1,
  },
  hover: {
    scale: 1.05,
    transition: {
      ...commonTransition,
      duration: 0.1,
    },
  },
  tap: {
    scale: 0.95,
  },
}; 