import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  CircularProgress,
  Alert,
  IconButton,
  useMediaQuery,
  CssBaseline,
  Snackbar,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  Button,
} from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';
import {
  Brightness4 as DarkIcon,
  Brightness7 as LightIcon,
  Notifications as NotificationsIcon,
  Lock as LockIcon,
} from '@mui/icons-material';
import { TaskForm } from './components/TaskForm';
import { TaskList } from './components/TaskList';
import { useTasks } from './hooks/useTasks';
import { Task, TaskFormData } from './types/Task';
import { lightTheme, darkTheme } from './theme';
import NotificationService from './services/NotificationService';

const LOCK_CODE_KEY = 'todo-lock-code';
const DEFAULT_LOCK_CODE = '2240';

const App: React.FC = () => {
  const { tasks, loading, error, notification, addTask, updateTask, deleteTask, clearNotification } = useTasks();
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const [isDarkMode, setIsDarkMode] = useState(prefersDarkMode);
  const [notificationEnabled, setNotificationEnabled] = useState(false);
  const [showNotificationPrompt, setShowNotificationPrompt] = useState(false);
  const [isLocked, setIsLocked] = useState(true);
  const [lockCode, setLockCode] = useState('');
  const [showLockDialog, setShowLockDialog] = useState(true);
  const [errorMessage, setErrorMessage] = useState('');

  // システムのカラーモード設定が変更された時に自動で切り替え
  useEffect(() => {
    setIsDarkMode(prefersDarkMode);
  }, [prefersDarkMode]);

  // 保存されたロックコードを読み込む
  useEffect(() => {
    const savedCode = localStorage.getItem(LOCK_CODE_KEY);
    if (!savedCode) {
      localStorage.setItem(LOCK_CODE_KEY, DEFAULT_LOCK_CODE);
    }
  }, []);

  // コードロックの処理
  const handleCodeSubmit = () => {
    const savedCode = localStorage.getItem(LOCK_CODE_KEY) || DEFAULT_LOCK_CODE;
    if (lockCode === savedCode) {
      setIsLocked(false);
      setShowLockDialog(false);
      setErrorMessage('');
      setLockCode('');
    } else {
      setErrorMessage('コードが正しくありません');
      setLockCode('');
    }
  };

  // コード入力の処理
  const handleCodeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value.replace(/[^0-9]/g, '');
    if (value.length <= 4) {
      setLockCode(value);
      setErrorMessage('');
      
      // 4桁入力完了時に自動チェック
      if (value.length === 4) {
        const savedCode = localStorage.getItem(LOCK_CODE_KEY) || DEFAULT_LOCK_CODE;
        if (value === savedCode) {
          setIsLocked(false);
          setShowLockDialog(false);
          setErrorMessage('');
          setLockCode('');
        } else {
          setErrorMessage('コードが正しくありません');
          setLockCode('');
        }
      }
    }
  };

  // 通知の初期化と監視の開始
  useEffect(() => {
    const notificationService = NotificationService.getInstance();

    const initializeNotifications = async () => {
      if (Notification.permission === 'default') {
        setShowNotificationPrompt(true);
      } else if (Notification.permission === 'granted') {
        setNotificationEnabled(true);
        notificationService.startChecking(tasks);
      }
    };

    initializeNotifications();

    return () => {
      notificationService.stopChecking();
    };
  }, [tasks]);

  const handleRequestNotification = async () => {
    const notificationService = NotificationService.getInstance();
    const granted = await notificationService.requestPermission();
    
    setShowNotificationPrompt(false);
    setNotificationEnabled(granted);
    
    if (granted) {
      notificationService.startChecking(tasks);
    }
  };

  const handleTaskSubmit = async (taskData: TaskFormData) => {
    try {
      await addTask(taskData);
    } catch (err) {
      console.error('Error handling task:', err);
    }
  };

  const handleToggleComplete = async (taskId: string, completed: boolean) => {
    try {
      await updateTask(taskId, { completed });
    } catch (err) {
      console.error('Error toggling task completion:', err);
    }
  };

  const toggleColorMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  if (isLocked) {
    return (
      <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
        <CssBaseline />
        <Dialog open={showLockDialog} onClose={() => {}} disableEscapeKeyDown>
          <DialogTitle sx={{ textAlign: 'center' }}>
            <LockIcon sx={{ fontSize: 40, mb: 1 }} />
            <Typography variant="h6">
              コードを入力してください
            </Typography>
          </DialogTitle>
          <DialogContent>
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column', 
              alignItems: 'center',
              gap: 2,
              p: 2 
            }}>
              <TextField
                type="password"
                value={lockCode}
                onChange={handleCodeChange}
                autoFocus
                inputProps={{
                  maxLength: 4,
                  pattern: '[0-9]*',
                  inputMode: 'numeric',
                  style: { 
                    textAlign: 'center',
                    letterSpacing: '0.5em',
                    fontSize: '1.5em'
                  }
                }}
                error={!!errorMessage}
                helperText={errorMessage}
                sx={{ width: '150px' }}
              />
            </Box>
          </DialogContent>
        </Dialog>
      </ThemeProvider>
    );
  }

  return (
    <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
      <CssBaseline />
      <Container maxWidth="md">
        <Box sx={{ my: 4 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
            <Typography variant="h4" component="h1" sx={{ flexGrow: 1 }} align="center">
              TODOリマインダー
            </Typography>
            <IconButton
              onClick={handleRequestNotification}
              color={notificationEnabled ? 'primary' : 'default'}
              disabled={notificationEnabled || Notification.permission === 'denied'}
              sx={{ ml: 2 }}
            >
              <NotificationsIcon />
            </IconButton>
            <IconButton onClick={toggleColorMode} color="inherit" sx={{ ml: 1 }}>
              {isDarkMode ? <LightIcon /> : <DarkIcon />}
            </IconButton>
          </Box>

          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}

          <TaskForm
            onSubmit={handleTaskSubmit}
          />

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <Box sx={{ mt: 4 }}>
              <TaskList
                tasks={tasks}
                onDelete={deleteTask}
                onToggleComplete={handleToggleComplete}
              />
            </Box>
          )}
        </Box>

        <Snackbar
          open={!!notification}
          autoHideDuration={10000}
          onClose={clearNotification}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          sx={{
            '& .MuiSnackbarContent-root': {
              minWidth: '300px',
            },
          }}
        >
          {notification && (
            <Alert
              onClose={clearNotification}
              severity={notification.type}
              variant="filled"
              elevation={6}
              sx={{
                width: '100%',
                '& .MuiAlert-message': {
                  fontSize: '1rem',
                },
              }}
            >
              {notification.message}
            </Alert>
          )}
        </Snackbar>

        <Snackbar
          open={showNotificationPrompt}
          message="タスクのリマインドを受け取るには通知を許可してください"
          action={
            <IconButton
              size="small"
              color="primary"
              onClick={handleRequestNotification}
            >
              <NotificationsIcon />
            </IconButton>
          }
        />
      </Container>
    </ThemeProvider>
  );
};

export default App; 