import { Task } from '../types/Task';

class NotificationService {
  private static instance: NotificationService;
  private checkInterval: number = 60000; // 1分ごとにチェック
  private intervalId?: number;
  private notificationSound?: HTMLAudioElement;
  private soundEnabled: boolean = false;
  private soundPath: string = '/notification.mp3';
  private lastCheckTime: string = '';

  private constructor() {
    if (typeof window !== 'undefined') {
      this.initializeSound();
    }
  }

  private initializeSound() {
    try {
      this.notificationSound = new Audio(this.soundPath);
      this.notificationSound.preload = 'auto';
      this.soundEnabled = true;
    } catch (error) {
      console.warn('通知音の初期化に失敗しました:', error);
      this.soundEnabled = false;
    }
  }

  public setSoundPath(path: string) {
    this.soundPath = path;
    this.initializeSound();
  }

  public static getInstance(): NotificationService {
    if (!NotificationService.instance) {
      NotificationService.instance = new NotificationService();
    }
    return NotificationService.instance;
  }

  public async requestPermission(): Promise<boolean> {
    if (!('Notification' in window)) {
      console.warn('このブラウザは通知をサポートしていません');
      return false;
    }

    if (Notification.permission === 'granted') {
      return true;
    }

    const permission = await Notification.requestPermission();
    return permission === 'granted';
  }

  public startChecking(tasks: Task[]) {
    if (this.intervalId) {
      this.stopChecking();
    }

    // 初回チェック
    this.checkTasks(tasks);

    // 次の分までの待機時間を計算
    const now = new Date();
    const nextMinute = new Date(now);
    nextMinute.setMinutes(now.getMinutes() + 1, 0, 0);
    const timeUntilNextCheck = nextMinute.getTime() - now.getTime();

    // 次の分まで待機してからチェックを開始
    setTimeout(() => {
      this.checkTasks(tasks);
      this.intervalId = window.setInterval(() => {
        this.checkTasks(tasks);
      }, this.checkInterval);
    }, timeUntilNextCheck);
  }

  public stopChecking() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
      this.intervalId = undefined;
    }
  }

  private async checkTasks(tasks: Task[]) {
    const now = new Date();
    const currentTime = this.formatTime(now);
    const currentDay = this.getDayOfWeek(now);
    const notificationKey = `notification_${now.toDateString()}`;

    // メモリ使用量を最適化するためにキャッシュをクリア
    this.clearOldNotificationCache();

    // バッチ処理による効率化
    const tasksToNotify = tasks.filter(task => {
      if (task.completed) return false;
      
      // 通知条件のチェック
      const shouldNotify = 
        task.reminderTime === currentTime &&
        (!task.reminderDays || task.reminderDays[currentDay]);

      // 重複通知防止
      const taskNotificationKey = `${notificationKey}_${task.id}`;
      const hasNotified = localStorage.getItem(taskNotificationKey);

      return shouldNotify && !hasNotified;
    });

    // バッチ通知の実行
    if (tasksToNotify.length > 0) {
      await this.showBatchNotifications(tasksToNotify);
    }
  }

  private clearOldNotificationCache() {
    const now = new Date();
    const yesterday = new Date(now);
    yesterday.setDate(yesterday.getDate() - 1);
    
    // 古い通知キャッシュを削除
    Object.keys(localStorage)
      .filter(key => key.startsWith('notification_'))
      .forEach(key => {
        const date = key.split('_')[1];
        if (new Date(date) < yesterday) {
          localStorage.removeItem(key);
        }
      });
  }

  private getDayOfWeek(date: Date): keyof Required<Task>['reminderDays'] {
    const days: Array<keyof Required<Task>['reminderDays']> = [
      'sunday',
      'monday',
      'tuesday',
      'wednesday',
      'thursday',
      'friday',
      'saturday',
    ];
    return days[date.getDay()];
  }

  private formatTime(date: Date): string {
    const hours = date.getHours().toString().padStart(2, '0');
    const minutes = date.getMinutes().toString().padStart(2, '0');
    return `${hours}:${minutes}`;
  }

  private async showBatchNotifications(tasks: Task[]) {
    if (Notification.permission !== 'granted') return;

    // 通知音は1回だけ再生
    if (this.soundEnabled && this.notificationSound) {
      try {
        this.notificationSound.currentTime = 0;
        await this.notificationSound.play();
      } catch (error) {
        console.warn('通知音の再生に失敗しました:', error);
      }
    }

    // タスクごとの通知を表示
    tasks.forEach(task => {
      const notification = new Notification('タスクのリマインド', {
        body: `${task.title}\n${task.description || ''}`,
        icon: '/favicon.ico',
        tag: task.id,
        requireInteraction: true,
      });

      notification.onclick = () => {
        window.focus();
        notification.close();
      };

      // 通知履歴を保存
      const notificationKey = `notification_${new Date().toDateString()}_${task.id}`;
      localStorage.setItem(notificationKey, 'true');
    });
  }
}

export default NotificationService; 