import React, { useState, useCallback, memo, useMemo } from 'react';
import {
  Box,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Stack,
  SelectChangeEvent,
  InputAdornment,
  useTheme,
  useMediaQuery,
  Paper,
  FormControlLabel,
  Switch,
} from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';
import { Task, TaskFormData, defaultReminderDays } from '../types/Task';
import {
  formVariants,
  formFieldVariants,
  buttonHoverVariants,
} from '../animations/variants';
import { DaySelector } from './DaySelector';

interface TaskFormProps {
  onSubmit: (task: TaskFormData) => void;
  initialTask?: Task;
}

const MotionPaper = motion(Paper);
const MotionButton = motion(Button);

const initialFormData: TaskFormData = {
  title: '',
  description: '',
  category: '',
  priority: 'normal',
  completed: false,
  reminderTime: '',
  reminderDays: defaultReminderDays,
  workTime: 0,
};

// メモ化されたフォームフィールドコンポーネント
const FormField = memo(({ children }: { children: React.ReactNode }) => (
  <motion.div
    variants={formFieldVariants}
    initial="hidden"
    animate="visible"
  >
    {children}
  </motion.div>
));

export const TaskForm: React.FC<TaskFormProps> = memo(({ onSubmit, initialTask }) => {
  const [formData, setFormData] = useState<TaskFormData>(
    initialTask ? {
      title: initialTask.title,
      description: initialTask.description || '',
      category: initialTask.category || '',
      priority: initialTask.priority,
      completed: initialTask.completed,
      reminderTime: initialTask.reminderTime || '',
      reminderDays: initialTask.reminderDays || defaultReminderDays,
      workTime: initialTask.workTime || 0,
    } : initialFormData
  );

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const selectedDays = useMemo(() => {
    if (!formData.reminderDays) return [];
    return Object.entries(formData.reminderDays)
      .filter(([_, isSelected]) => isSelected)
      .map(([day]) => day);
  }, [formData.reminderDays]);

  const handleSubmit = useCallback((e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
    if (!initialTask) {
      setFormData(initialFormData);
    }
  }, [formData, initialTask, onSubmit]);

  const handleChange = useCallback((
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent
  ) => {
    const { name, value } = e.target;
    if (name === 'workTime') {
      const numValue = parseInt(value) || 0;
      setFormData(prev => ({
        ...prev,
        [name]: numValue,
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value,
      }));
    }
  }, []);

  const handleDaysChange = useCallback((days: string[]) => {
    setFormData(prev => ({
      ...prev,
      reminderDays: {
        sunday: days.includes('sunday'),
        monday: days.includes('monday'),
        tuesday: days.includes('tuesday'),
        wednesday: days.includes('wednesday'),
        thursday: days.includes('thursday'),
        friday: days.includes('friday'),
        saturday: days.includes('saturday'),
      },
    }));
  }, []);

  return (
    <AnimatePresence mode="wait">
      <MotionPaper
        variants={formVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
        layout
        sx={{ p: 2 }}
      >
        <Box
          component="form"
          onSubmit={handleSubmit}
          sx={{
            maxWidth: 600,
            margin: '0 auto',
            width: '100%',
          }}
        >
          <Stack spacing={2}>
            <FormField>
              <TextField
                required
                fullWidth
                label="タイトル"
                name="title"
                value={formData.title}
                onChange={handleChange}
                size={isMobile ? 'small' : 'medium'}
              />
            </FormField>

            <FormField>
              <TextField
                fullWidth
                label="説明"
                name="description"
                multiline
                rows={isMobile ? 2 : 3}
                value={formData.description}
                onChange={handleChange}
                size={isMobile ? 'small' : 'medium'}
              />
            </FormField>

            <Stack
              direction={isMobile ? 'column' : 'row'}
              spacing={2}
              sx={{ width: '100%' }}
            >
              <FormField>
                <TextField
                  fullWidth
                  label="カテゴリー"
                  name="category"
                  value={formData.category}
                  onChange={handleChange}
                  size={isMobile ? 'small' : 'medium'}
                />
              </FormField>

              <FormField>
                <FormControl fullWidth size={isMobile ? 'small' : 'medium'}>
                  <InputLabel>優先度</InputLabel>
                  <Select
                    name="priority"
                    value={formData.priority}
                    label="優先度"
                    onChange={handleChange}
                  >
                    <MenuItem value="normal">通常</MenuItem>
                    <MenuItem value="high">最優先</MenuItem>
                  </Select>
                </FormControl>
              </FormField>
            </Stack>

            <Stack
              direction={isMobile ? 'column' : 'row'}
              spacing={2}
              sx={{ width: '100%' }}
            >
              <FormField>
                <TextField
                  fullWidth
                  label="リマインド時刻"
                  name="reminderTime"
                  type="time"
                  value={formData.reminderTime || ''}
                  onChange={handleChange}
                  InputLabelProps={{
                    shrink: true,
                  }}
                  inputProps={{
                    step: 300, // 5分単位
                  }}
                  size={isMobile ? 'small' : 'medium'}
                />
              </FormField>

              <FormField>
                <TextField
                  fullWidth
                  label="作業時間"
                  name="workTime"
                  type="number"
                  value={formData.workTime}
                  onChange={handleChange}
                  InputProps={{
                    endAdornment: <InputAdornment position="end">分</InputAdornment>,
                    inputProps: { min: 0, step: 5 }
                  }}
                  size={isMobile ? 'small' : 'medium'}
                />
              </FormField>
            </Stack>

            {formData.reminderTime && (
              <FormField>
                <DaySelector
                  value={selectedDays}
                  onChange={handleDaysChange}
                  disabled={!formData.reminderTime}
                />
              </FormField>
            )}

            {formData.reminderDays && Object.values(formData.reminderDays).some(day => day) && (
              <FormField>
                <FormControlLabel
                  control={
                    <Switch
                      checked={!!formData.isLoopReminder}
                      onChange={(e) => setFormData(prev => ({
                        ...prev,
                        isLoopReminder: e.target.checked
                      }))}
                    />
                  }
                  label="毎週繰り返す"
                />
              </FormField>
            )}

            <MotionButton
              component={motion.button}
              variants={buttonHoverVariants}
              initial="initial"
              whileHover="hover"
              whileTap="tap"
              type="submit"
              variant="contained"
              color="primary"
              fullWidth
              size={isMobile ? 'medium' : 'large'}
              sx={{ mt: 2 }}
            >
              {initialTask ? 'タスクを更新' : 'タスクを追加'}
            </MotionButton>
          </Stack>
        </Box>
      </MotionPaper>
    </AnimatePresence>
  );
}); 