import React, { useState, useEffect, useCallback } from 'react';
import {
  Box,
  Typography,
  IconButton,
  LinearProgress,
  Paper,
} from '@mui/material';
import {
  PlayArrow as PlayIcon,
  Pause as PauseIcon,
  Stop as StopIcon,
} from '@mui/icons-material';

interface TaskTimerProps {
  taskId: string;
  taskTitle: string;
  totalMinutes: number;
  onComplete: () => void;
}

export const TaskTimer: React.FC<TaskTimerProps> = ({
  taskId,
  taskTitle,
  totalMinutes,
  onComplete,
}) => {
  const [remainingSeconds, setRemainingSeconds] = useState(totalMinutes * 60);
  const [isRunning, setIsRunning] = useState(true);
  const [progress, setProgress] = useState(100);

  const formatTime = (seconds: number): string => {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const remainingSecs = seconds % 60;
    
    if (hours > 0) {
      return `${hours}:${minutes.toString().padStart(2, '0')}:${remainingSecs.toString().padStart(2, '0')}`;
    }
    return `${minutes}:${remainingSecs.toString().padStart(2, '0')}`;
  };

  const handleStart = useCallback(() => setIsRunning(true), []);
  const handlePause = useCallback(() => setIsRunning(false), []);
  const handleStop = useCallback(() => {
    setIsRunning(false);
    onComplete();
  }, [onComplete]);

  useEffect(() => {
    let timer: NodeJS.Timeout;

    if (isRunning && remainingSeconds > 0) {
      timer = setInterval(() => {
        setRemainingSeconds((prev) => {
          const next = prev - 1;
          setProgress((next / (totalMinutes * 60)) * 100);
          
          if (next === 0) {
            setTimeout(() => {
              handleStop();
            }, 0);
          }
          
          return next;
        });
      }, 1000);
    }

    return () => {
      if (timer) {
        clearInterval(timer);
      }
    };
  }, [isRunning, remainingSeconds, totalMinutes, handleStop]);

  useEffect(() => {
    if ('Notification' in window && Notification.permission === 'default') {
      Notification.requestPermission();
    }
  }, []);

  return (
    <Paper sx={{ p: 2, mb: 2 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
        <Typography variant="h6" sx={{ flexGrow: 1, mr: 2 }}>
          {taskTitle}
        </Typography>
        <Typography variant="h4" sx={{ fontFamily: 'monospace', mr: 2 }}>
          {formatTime(remainingSeconds)}
        </Typography>
        <Box>
          {!isRunning ? (
            <IconButton
              color="primary"
              onClick={handleStart}
              disabled={remainingSeconds === 0}
            >
              <PlayIcon />
            </IconButton>
          ) : (
            <IconButton color="primary" onClick={handlePause}>
              <PauseIcon />
            </IconButton>
          )}
          <IconButton
            color="secondary"
            onClick={handleStop}
          >
            <StopIcon />
          </IconButton>
        </Box>
      </Box>
      <LinearProgress
        variant="determinate"
        value={progress}
        sx={{
          height: 8,
          borderRadius: 4,
          backgroundColor: theme => theme.palette.grey[200],
          '& .MuiLinearProgress-bar': {
            backgroundColor: remainingSeconds < 60 ? '#f44336' : undefined,
            transition: 'background-color 0.3s ease',
          },
        }}
      />
    </Paper>
  );
}; 