import { useState, useEffect, useCallback } from 'react';
import { Task, TaskFormData } from '../types/Task';

const STORAGE_KEY = 'todo-tasks';
const TASKS_PER_PAGE = 20;

export interface TaskNotification {
  message: string;
  type: 'success' | 'info' | 'warning' | 'error';
}

export const useTasks = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [hasMore, setHasMore] = useState(true);
  const [notification, setNotification] = useState<TaskNotification | null>(null);

  // 初期データの読み込み
  useEffect(() => {
    try {
      const savedTasks = localStorage.getItem(STORAGE_KEY);
      if (savedTasks) {
        const parsedTasks = JSON.parse(savedTasks).map((task: any) => ({
          ...task,
          createdAt: new Date(task.createdAt),
          updatedAt: new Date(task.updatedAt),
        }));
        setTasks(parsedTasks);
      }
    } catch (err) {
      console.error('Error loading tasks:', err);
      setError('タスクの読み込み中にエラーが発生しました');
      setNotification({
        message: 'タスクの読み込み中にエラーが発生しました',
        type: 'error'
      });
    } finally {
      setLoading(false);
    }
  }, []);

  // タスクが更新されたらローカルストレージに保存
  useEffect(() => {
    if (tasks.length > 0) {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(tasks));
    }
  }, [tasks]);

  const addTask = async (taskData: TaskFormData) => {
    try {
      const now = new Date();
      const newTask: Task = {
        id: Date.now().toString(),
        ...taskData,
        createdAt: now,
        updatedAt: now,
      };

      setTasks(prev => [newTask, ...prev]);
      setNotification({
        message: `タスク「${newTask.title}」を追加しました`,
        type: 'success'
      });
    } catch (err) {
      console.error('Error adding task:', err);
      setError('タスクの追加中にエラーが発生しました');
      setNotification({
        message: 'タスクの追加中にエラーが発生しました',
        type: 'error'
      });
      throw err;
    }
  };

  const updateTask = async (taskId: string, taskData: Partial<Task>) => {
    try {
      const now = new Date();
      setTasks(prev => prev.map(task =>
        task.id === taskId
          ? { ...task, ...taskData, updatedAt: now }
          : task
      ));
      setNotification({
        message: 'タスクを更新しました',
        type: 'success'
      });
    } catch (err) {
      console.error('Error updating task:', err);
      setError('タスクの更新中にエラーが発生しました');
      setNotification({
        message: 'タスクの更新中にエラーが発生しました',
        type: 'error'
      });
      throw err;
    }
  };

  const deleteTask = async (taskId: string) => {
    try {
      const taskToDelete = tasks.find(task => task.id === taskId);
      setTasks(prev => prev.filter(task => task.id !== taskId));
      if (taskToDelete) {
        setNotification({
          message: `タスク「${taskToDelete.title}」を削除しました`,
          type: 'info'
        });
      }
    } catch (err) {
      console.error('Error deleting task:', err);
      setError('タスクの削除中にエラーが発生しました');
      setNotification({
        message: 'タスクの削除中にエラーが発生しました',
        type: 'error'
      });
      throw err;
    }
  };

  const clearNotification = useCallback(() => {
    setNotification(null);
  }, []);

  const loadMoreTasks = useCallback(async () => {
    // ローカルストレージでは不要なため、空の実装
  }, []);

  return {
    tasks,
    loading,
    error,
    hasMore,
    notification,
    addTask,
    updateTask,
    deleteTask,
    loadMoreTasks,
    clearNotification,
  };
}; 